.checkbox-root{
  color: white;
  display: flex;
  grid-gap: 20px;
  flex-direction: row-reverse;
  direction: rtl;
}

label {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all 300ms;
  cursor: pointer;
}

.sr-only {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.c-checkbox {
  position: relative;
  color: white;
}
.c-checkbox input {
  transition: all 200ms;
}
.c-checkbox input + label {
  transition: all 200ms;
  border-radius: 1px;
  width: auto;
}
.c-checkbox input:focus:not(:focus-visible) {
  outline: none;
}
.c-checkbox input:focus + .c-checkbox__label {
  position: relative;
}
.c-checkbox input:focus + .c-checkbox__label::after {
  border-radius: 2rem;
  outline: dashed 2px rgba(65, 105, 225, 0.5);
  outline-offset: 2px;
  padding: 0.5em;
  content: "";
  position: absolute;
  display: inline-block;
  min-width: 120%;
  min-height: 200%;
  left: -8%;
  top: 0;
  margin: auto 0;
  vertical-align: middle;
  bottom: 0;
  right: 0;
  border-radius: 2rem;
  background-color: rgba(65, 105, 225, 0.125);
}

.c-checkbox__label {
  cursor: pointer;
  display: inline-block;
}
.c-checkbox__label::before {
  content: "" !important;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border: 2px solid #c5c5c5;
  margin-right: 8px;
}
.c-checkbox__label:hover {
  color: blue;
  color: #214cce;
}
.c-checkbox__label:hover::before {
  background-color: whitesmoke;
}

.c-checkbox--svg input:checked + .c-checkbox__label--svg::before {
  background: #3863d9;
  border-color: #3863d9;
}
.c-checkbox--svg input:checked + .c-checkbox__label--svg .checkbox-icon-svg-path {
  stroke: #fff;
  stroke-dashoffset: 0;
}

.c-checkbox__label--svg {
  padding-left: 2em;
  font-weight: 500;
  font-size: 1em;
}
.c-checkbox__label--svg::before {
  position: absolute;
  left: 0;
  top: -2px;
  width: 24px;
  height: 24px;
  border-radius: 0.25em;
  transition: all 0.125s ease-out;
}
.c-checkbox__label--svg svg {
  position: absolute;
  left: 3px;
  top: 1px;
  width: 18px;
  height: 18px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.checkbox-icon-svg-path {
  stroke-dashoffset: 111.46px;
  stroke-dasharray: 111.46px;
  stroke: #3863d9;
  transition: all 0.125s ease-out;
  transition-delay: 50ms;
}
@media screen and (max-width: 1200px) {
  .wrapper{
    flex-direction: column;
  }
.convert-purchase{
  top: -50px;
  position: relative;
  text-align: center;
}
.convert-info{
  margin-left: 0;
  top: -50px;
}
.Textarea-root{
  max-width: 100%;
}
textarea{
  width: auto;
}
}
