@tailwind base;
@tailwind components;
@tailwind utilities;

@import url(./styles.css);
@import url(./convertHtmlToReact.css);


:root {
  --foreground-rgb: 0, 0, 0;
  --background-start-rgb: 214, 219, 220;
  --background-end-rgb: 255, 255, 255;
}

@media (prefers-color-scheme: dark) {
  :root {
    --foreground-rgb: 255, 255, 255;
    --background-start-rgb: 0, 0, 0;
    --background-end-rgb: 0, 0, 0;
  }
}

html {
  scroll-behavior: smooth !important;
}

body {
  color: rgb(var(--foreground-rgb));
  background: #1d1552;
  scroll-behavior: smooth;
}
.flex{
  display: flex;
  grid-gap: 20px;
}
.header {
  height: 100px;
}

nav {
  background-color: rgba(47, 59, 79, 0.9);
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  margin-bottom: 50px;
}

.flexSB {
  display: flex;
  justify-content: space-between;
}

.flexSB ul {
  padding: 30px 20px;
}

.start {
  background-color: #172d52;
  padding: 30px 70px;
  color: #fff;
  width: 415px;
  clip-path: polygon(10% 0%, 100% 0, 100% 100%, 0% 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  text-transform: uppercase;
}

.flexSB a {
  color: #fff;
  font-weight: 500;
  transition: 0.5s;
}
.hamberger{
  color: white;
  font-weight: 700;
  font-size: 20px;
  display: flex;
  grid-gap: 10px;
}
.flexSB li {
  margin-right: 40px;
}
.home{
  height: 85vh;
  margin-top: -70px;
}
@media screen and (max-width: 1200px) {
  .flexSB ul{
    grid-gap: 20px;
    flex-wrap: wrap;
  }
  .flexSB li {
    font-size: 13px;
  }
  .start {
    background-color: #172d52;
    padding: 30px 30px;
    color: #fff;
    width: auto;
    font-size: 15px;
    margin-right: 150px;
    clip-path: polygon(10% 0%, 100% 0, 100% 100%, 0% 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    text-transform: uppercase;
  }
}